//
.Registration_form {
    padding: 0.5rem;
    max-width: 500px;

    background-image: linear-gradient(45deg, var(--success), var(--blue-tear));

    /* btn ripple bg */
    .App_submit display-flex-center .ButtonRipple {
        background-color: var(--green);
    }
}

.Registration_error {
    margin-top: 1rem;
    padding: 0.5rem;
    border: 2px solid var(--danger);
    background-color: var(--heart-blur);
}

.Registration_input {
    margin: 1rem 0;
    input {
        border-radius: 20px;
        padding: 8px 15px;
    }
}
